<template>
  <div id="members">
        <Card class="tagTitle">    
            <p slot="title">研究分类</p>  
            <Tag type="border" class="tag" v-for="(item,index) in reserch_type" :color="tagColor[index%4]" :key="index" :name="index" closable @on-close="deletTag">
                {{item.reserch_name}}
            </Tag><br>
            <Button class="add" icon="ios-plus-empty" type="dashed" size="small" @click="handleAdd">添加分类</Button>
        </Card>
        <Card>
            成员分类查询：           
            <Select v-model="tagType" style="width:200px" @on-change="changeTag">
                <Option v-for="item in reserch_type" :value="item.reserch_num" :key="item.reserch_num">
                    {{ item.reserch_name }}
                </Option>
            </Select>
        </Card>
        <div style="text-align:center;">
            <Table :loading="loading" :columns="columns" :data="allMembers" stripe></Table>     
        </div>  
  </div>
</template>

<script>
export default {
    name: 'members',
    data () {
        return {
            loading: false,
            columns: [
                {
                    title: '姓名',
                    key: 'username',
                    render: (h, params) => {
                        return h('div', [
                            h('Icon', {
                                props: {
                                    type: 'person'
                                }
                            }),
                            h('strong', params.row.username)
                        ]);
                    }
                },
                {
                    title: '学号',
                    key: 'student_num'
                },
                {
                    title: '学院',
                    key: 'depart_name',
                    render: (h,params) => {
                        if(params.row.depart_name){
                            return h('div',
                            `${params.row.depart_name}`)
                        }else{
                            return h('div',
                                "未完善"
                            )
                        }
                    }
                },
                {
                    title: '专业',
                    key: 'major_name',
                    render: (h,params) => {
                        if(params.row.depart_name){
                            return h('div',
                            `${params.row.major_name}`)
                        }else{
                            return h('div',
                                "未完善"
                            )
                        }
                    }
                },               
                {
                    title: '操作',
                    key: 'action',
                    width: 150,
                    align: 'center',
                    render: (h, params) => {
                        return h('div', [                                
                            h('Button', {
                                props: {
                                    type: 'error',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {                         
                                        this.delUser(params.row.student_num);
                                    }
                                }
                            }, '踢出成员')
                        ]);
                    }
                }
            ],
            allMembers: [],
            reserch_type: [],
            tagType: "",
            tagColor:['blue','green','red','yellow']      
        }
    },
    created(){
        this.sellectAllMembers();       
        this.sellectAllReserch();
    },
    methods:{   
        sellectAllReserch(){
            this.$http.get('/members/selectAllSearch').then(res => {
                this.reserch_type = res.data.msg;
            }).catch(err => {
                this.$Message.error("出错啦。。。")
            })
        },
        sellectAllMembers(){
            this.$http.get('/members/selectAllMembers').then(res =>{
                this.allMembers = res.data.msg;           
            }).catch(err => {
                this.$Message.error("出错啦。。。")
            }) 
        },
        delUser(student_num){
            this.$http.get('/user/deluser',{
                params: {
                    'studentNum': student_num
                }
            }).then(res => {
                this.$Message.info("删除成功！");
                this.sellectAllMembers();
            }).catch(err => {
                this.$Message.error("出错啦。。。")
            })
        },           
        deletTag (event,index) {
            this.$Modal.confirm({
                content: '<p>确定删除吗</p>',
                onOk: () => {
                    this.$http.post('/members/reserchDel',{
                        'reserchNum': this.reserch_type[index].reserch_num
                    }).then(res => {
                        this.$Modal.remove();
                        this.$Message.info('删除成功！');                        
                        this.sellectAllReserch();
                    }).catch(err => {
                        his.$Modal.remove();
                        this.$Message.error('删除失败！');
                    })
                },
                onCancel: ()=> {
                    this.$Message.info('取消成功！');
                }
            })
                   
        },
        handleAdd () {
            this.$Modal.confirm({
                render: (h) => {
                    return h('Input', {
                        props: {
                            value: this.value,
                            autofocus: true,
                            placeholder: '请输入新的研究方向名称'
                        },
                        on: {
                            input: (val) => {                                
                                this.value = val;
                            }
                        }                        
                    })
                },
                loading: true,
                onOk: () => {
                    if(this.value == null){
                        this.$Modal.remove();
                        this.$Message.error('内容不能为空！');
                    }else{
                        this.$http.post('/members/reserchAdd',{
                            'reserchName': this.value
                        }).then(res => {                        
                            this.$Modal.remove();
                            this.sellectAllReserch();
                            this.$Message.info("添加成功！");                        
                            this.value = "";
                        }).catch(err => {
                            this.$Modal.remove();
                            this.$Message.error("添加失败！");    
                        })
                    }
                },
                onCancel: () => {
                    this.value = ""
                }
            })
        },
        changeTag (reserch_num) {
            this.loading = true;
            this.$http.get('/members/selectByTag',{
                params:{
                    "reserchNum":reserch_num
                }
            }).then(res => {
                this.loading = false;
                this.allMembers = res.data.msg;
            }).catch(err => {
                this.loading = false;
                this.$Message.error("出错啦。。。")
            })
        }
    }
}
</script>
<style lang="scss" scoped>
.tagTitle{
    margin-top: 10px;
    margin-bottom: 20px;
    .tag{
        margin-right: 20px;
    }
    .add{
        margin-top: 10px;
    }
}
.page{
    margin-top: 10px;
}
</style>




